.body {
    font-family: serif;
    font-style: italic;
}

.gameBoard,
.preGame,
.gameOver {
    text-align: center;
    height: 100vh;
    width: 100vw;
}

.gameBoard {
    display: grid;
    grid-template-columns: auto auto auto;
}

.preGame,
.gameOver {
    -ms-flex-line-pack: center;
        align-content: center;
    background: rgba(255, 255, 255, 0.9);
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 100%;
    -ms-flex-pack: center;
        justify-content: center;
    position: absolute;
    width: 100%;
}

.controls {
    -ms-flex-line-pack: center;
        align-content: center;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    left: calc(50% - 100px);
    margin: 4px 0;
    padding: 8px;
    position: absolute;
    text-align: center;
    top: 0;
    width: 200px;
}

.preGame .playerSelect {
    background: #ddd;
    border-radius: 100px;
    border: none;
    cursor: pointer;
    font-size: 24px;
    height: 50px;
    margin: 0 15px;
    width: 50px;
}

.gameOver a {
    cursor: pointer;
    text-decoration: underline;
}

.label {
    display: block;
    font-size: 48px;
}

.position {
    -ms-flex-align: center;
        align-items: center;
    display: -ms-flexbox;
    display: flex;
    font-size: 100px;
    -ms-flex-pack: center;
        justify-content: center;
}

.availablePosition {
    background-color: #EEE;
    border: 1px solid #DDD;
}

.availablePosition:focus {
    outline: 0;
}

.position,
.availablePosition {
    height: calc(100vh / 3);
    width: calc(100vw / 3);
}

.gameBoard :nth-child(1),
.gameBoard :nth-child(4),
.gameBoard :nth-child(7) {
    border-left: 0;
}

.gameBoard :nth-child(1),
.gameBoard :nth-child(2),
.gameBoard :nth-child(3) {
    border-top: 0;
}

.gameBoard :nth-child(3),
.gameBoard :nth-child(6),
.gameBoard :nth-child(9) {
    border-right: 0;
}

.gameBoard :nth-child(7),
.gameBoard :nth-child(8),
.gameBoard :nth-child(9) {
    border-bottom: 0;
}


